body {
  margin: 0;
  font-family: Roboto, serif;
}

.header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding-left: 50px;
  padding-right: 30px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list-item {
  text-transform: uppercase;
  color: #060b35;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__list-item:last-child {
  margin-right: 20px;
}

.nav__list-item:hover {
  color: #00acdc;
}

.nav__list-item.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  transition: width .3s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__list-item:hover:after {
  width: 100%;
}

.is-active {
  color: #00acdc;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  position: relative;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__code {
  color: #616070;
  text-align: left;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  position: relative;
}

.card__stars-container {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  align-items: flex-end;
  margin-bottom: 24px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__cost {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price {
  color: #616070;
  text-align: left;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
  position: relative;
}

.card__total {
  color: #060b35;
  align-items: flex-end;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  display: flex;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  box-sizing: border-box;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  position: absolute;
  right: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__name {
  color: #34568b;
}

p {
  margin: 0;
}

.card-stars {
  gap: 4px;
  margin-bottom: 24px;
  display: flex;
}

.card-stars__star {
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.card-stars__star:first-child {
  margin-left: 0;
}

.card-stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card-stars__star:nth-child(5) {
  background-image: url("star.822d561e.svg");
}

.cardcontainer {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  grid-template-rows: auto;
  justify-content: center;
  gap: 46px 48px;
  margin: 0;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cardcontainer {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .cardcontainer {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .cardcontainer {
    --column-count: 4;
  }
}
